<template>
  <el-container>
    <el-main>
      <el-row class="class143">
        <el-col :span="5">
          <a
            href=""
            v-on:click="change_Achievement_acknowlege"
            style="color: #999999"
            >我的成果
          </a>
        </el-col>

        <el-col :span="2"> &gt; </el-col>
        <el-col :span="5">
          <a href="" v-on:click="change_my_project" style="color: #0064f9"
            >成果管理</a
          >
        </el-col>
      </el-row>

      <el-divider class="class144"> </el-divider>

      <div>
        <span class="class145_a class145">
          <img class="class146_a" src="imgs/论文.png" />
          <div class="class147_a">论文（{{ achievement.paper_num }}）</div>
        </span>
        <span class="class145_b class145">
          <div class="class146_b">
            <img src="imgs/专利.png" />
          </div>
          <div class="class147_b">专利（{{ achievement.patent_num }}）</div>
        </span>
        <span class="class145_c class145">
          <div class="class146_c">
            <img src="imgs/项目.png" />
          </div>
          <div class="class147_c">项目（{{ achievement.project_num }}）</div>
        </span>
        <span class="class145_d class145">
          <div class="class146_d">
            <img src="imgs/软件著作权.png" />
          </div>
          <div class="class147_d">
            软件著作权（{{ achievement.software_copyright }}）
          </div>
        </span>
        <span class="class145_e class145">
          <div class="class146_e">
            <img src="imgs/获奖.png" />
          </div>
          <div class="class147_e">获奖（{{ achievement.award }}）</div>
        </span>
        <span class="class145_f class145">
          <div class="class146_f">
            <img src="imgs/学生获奖.png" />
          </div>
          <div class="class147_f">
            学生获奖（{{ achievement.student_award }}）
          </div>
        </span>
      </div>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "my_project",
  data() {
    return {
      achievement: {
        paper_num: 120,
        patent_num: 1,
        project_num: 4,
        software_copyright: 0,
        award: 1,
        student_award: 0,
      },
      id: "", //唯一标识
    };
  },
  methods: {
    change_Achievement_acknowlege() {
      this.$router.push({
        name: "Achievement_acknowlege",
        query: {
          id: this.id,
        },
      });
    },
    change_my_project() {
      this.$router.push({
        name: "my_project",
        query: {
          id: this.id,
        },
      });
    },
  },
  mounted() {
    document
      .querySelector("body")
      .setAttribute("style", "background-color:white");
  },
  beforeDestroy() {
    document.querySelector("body").removeAttribute("style");
  },
};
</script>

