<template>
  <el-container>
    <el-aside width="220px" class="class0">
      <el-scrollbar style="height: 100%">
        <el-menu
          :default-openeds="['1', '2']"
          text-color="#fff"
          active-text-color="#ffd04b"
          style="top: 79px"
        >
          <a href="/Member_management">
            <el-menu-item>
              <img src="imgs/管理中心.png" class="class0_a" />
              <span slot="title"> 成员管理 </span>
            </el-menu-item>
          </a>

          <el-submenu index="1">
            <template slot="title">
              <img src="imgs/文化成果.png" class="class0_a" />
              <span style="color: white"> 成果管理 </span>
            </template>
            <el-menu-item-group>
              <a href="/Achievement">
                <el-menu-item index="2-1" style="font-size: 16px">
                  成果报告
                </el-menu-item>
              </a>
              <a href="/Achievement_analysis">
                <el-menu-item index="2-2" style="font-size: 16px">
                  成果分析
                </el-menu-item>
              </a>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-scrollbar>
    </el-aside>

    <el-main class="class11">
      <el-row>
        <el-col :span="3">
          <div class="class7">年度成果报告</div>
        </el-col>
        <el-col :span="4">
          <div class="class8">
            <el-button
              type="primary"
              size="medium"
              @click="dialogVisible = true"
              >创建成果报告</el-button
            >
            <el-dialog
              :visible.sync="dialogVisible"
              custom-class="class86"
              center
              width="604px"
            >
              <div class="class87">
                <img src="/imgs/核对信息、报告.png" />
              </div>
              <div class="class88">创建成果报告</div>

              <el-row>
                <el-col>
                  <div class="class89">
                    <el-select
                      v-model="begin_year"
                      placeholder="请选择开始年份"
                      style="width: 155px"
                    >
                      <el-option
                        v-for="num in 20"
                        :key="num"
                        :label="year_now - num + 1"
                        :value="year_now - num + 1"
                      >
                      </el-option>
                    </el-select>
                  </div>
                </el-col>
                <el-col class="class90">
                  <div>至</div>
                </el-col>
                <el-col class="class91">
                  <el-select
                    v-model="end_year"
                    placeholder="请选择结束年份"
                    style="width: 155px"
                  >
                    <el-option
                      v-for="num in 20"
                      :key="num"
                      :label="year_now - num + 1"
                      :value="year_now - num + 1"
                    >
                    </el-option>
                  </el-select>
                </el-col>
              </el-row>

              <!-- 此处是做一个展示，照理来讲应该要用form和后端进行对接 -->
              <el-row class="class92">
                <a href="/Result_report_detail">
                  <el-button type="primary">生成成果报告</el-button>
                </a>
              </el-row>
            </el-dialog>
          </div>
        </el-col>
      </el-row>

      <div class="class9">
        <el-row v-for="num in 10" :key="num">
          <el-col :span="5">
            <div>
              {{ year - num + 1 }}年1月1日——{{
                year - num + 1
              }}年12月31日研究成果统计
            </div>
          </el-col>
        </el-row>

        <!-- <el-button size="small" @click="pervious" class="class178"
          >上一页</el-button
        > -->

        <el-pagination
          background
          prev-text="< 上一页"
          next-text="下一页 >"
          layout="prev,pager,next"
          :total="total"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="PageSize"
          ref="pageGroup"
        >
        </el-pagination>

        <!-- <el-button size="small" @click="next" class="class179"
          >下一页</el-button
        > -->
      </div>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "Achievement",
  data() {
    return {
      dialogVisible: false,
      year: new Date().getFullYear(),
      year_now: new Date().getFullYear(),
      begin_year: "",
      end_year: "",
      total: 20,
      currentPage: 1,
      PageSize: 10,
    };
  },
  methods: {
    handleCurrentChange(val) {
      // 改变默认的页数,切换页码时，要获取每页显示的条数
      this.currentPage = val;
      this.year = new Date().getFullYear() - (val - 1) * this.PageSize;
    },
    pervious() {
      this.$refs.pageGroup.prev();
    },
    next() {
      this.$refs.pageGroup.next();
    },
  },
  mounted() {
    document
      .querySelector("body")
      .setAttribute("style", "background-color:white");
  },
  beforeDestroy() {
    document.querySelector("body").removeAttribute("style");
  },
};
</script>

<style>
</style>