<template>
  <el-container>
    <el-main>
      <el-row class="class144_b">
        <el-col :span="5">
          <a
            href=""
            v-on:click="change_Achievement_acknowlege"
            style="color: #999999"
            >我的成果
          </a>
        </el-col>

        <el-col :span="1"> &gt; </el-col>
        <el-col :span="3">
          <a href="" v-on:click="change_project" style="color: #0064f9">项目</a>
        </el-col>
      </el-row>

      <el-button type="primary" class="class106" @click="dialogVisible = true"
        >新增项目</el-button
      >
      <el-dialog :visible.sync="dialogVisible" custom-class="class149" center>
        <a href="" @click="change_Results_search">
          <span>
            <img src="imgs/教师端-项目/弹窗-背景.png" class="class150" />
          </span>
          <span>
            <img src="imgs/教师端-项目/搜索.png" class="class151" />
          </span>
          <span class="class152"> 搜索认领 </span>
          <span class="class153"> 输入关键词，搜索你的科研成果并认领 </span>
        </a>

        <el-upload
          :action="UploadUrl"
          class="upload-demo"
          accept=".xlsx"
          auto-upload="true"
          :on-success="UploadSuccess"
        >
          <span class="class157"> </span>
          <span>
            <img src="imgs/教师端-项目/上传.png" class="class154" />
          </span>
          <span class="class155"> 上传成果模板 </span>
          <span class="class156"> 请先下载当前成果模板，完善信息后上传 </span>
        </el-upload>
      </el-dialog>

      <el-divider class="class107"></el-divider>

      <div class="class108">全部{{ project_number }}个</div>

      <el-row
        :gutter="20"
        v-for="(project, index) in projects"
        :key="index"
        :style="{ top: 186 + Math.floor(index / 3) * 200 + 'px' }"
        :class="setClass(index)"
      >
        <el-col :span="6">
          <el-col><img src="imgs/下载模板.png" class="class110" /></el-col>
          <el-col>
            <div class="class112">
              {{ project.name }}
            </div>
            <div>
              <span>
                <img class="class111" src="imgs/下载.png" />
              </span>
              <span>
                <a :href="get_url(project.url)" class="class113">点击下载</a>
              </span>
            </div>
          </el-col>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "project",
  data() {
    return {
      project_number: "3",
      projects: [
        {
          name: "横向课题模板",
          url: "files/成员信息模板.xlsx",
        },
        {
          name: "纵向课题模板",
          url: "files/成员信息模板.xlsx",
        },
        {
          name: "模板3",
          url: "files/成员信息模板.xlsx",
        },
        {
          name: "模板4",
          url: "files/成员信息模板.xlsx",
        },
      ],
      dialogVisible: false,
      id: 1,
    };
  },
  methods: {
    get_url: function (url) {
      return url;
    },
    setClass: function (num) {
      return num % 3 == 0
        ? "class109"
        : num % 3 == 1
        ? "class109_a"
        : "class109_b";
    },
    //搭配后端url用
    UploadUrl() {
      return "";
    },
    change_Achievement_acknowlege() {
      this.$router.push({
        name: "Achievement_acknowlege",
        query: {
          id: this.id,
        },
      });
    },
    change_project() {
      this.$router.push({
        name: "project",
        query: {
          id: this.id,
        },
      });
    },
    change_Results_search() {
      this.$router.push({
        name: "Results_search",
        query: {
          id: this.id,
        },
      });
    },
  },
  mounted() {
    document
      .querySelector("body")
      .setAttribute("style", "background-color:white");
  },
  beforeDestroy() {
    document.querySelector("body").removeAttribute("style");
  },
};
</script>

<style>
body {
  background-color: white;
}
</style>