<template>
  <el-container>
    <el-aside width="220px" class="class0">
      <el-scrollbar style="height: 100%">
        <el-menu
          :default-openeds="['1', '2']"
          text-color="#fff"
          active-text-color="#ffd04b"
          style="top: 79px"
        >
          <a href="/Member_management">
            <el-menu-item>
              <img src="imgs/管理中心.png" class="class0_a" />
              <span slot="title"> 成员管理 </span>
            </el-menu-item>
          </a>

          <el-submenu index="1">
            <template slot="title">
              <img src="imgs/文化成果.png" class="class0_a" />
              <span style="color: white"> 成果管理 </span>
            </template>
            <el-menu-item-group>
              <a href="/Achievement">
                <el-menu-item index="2-1" style="font-size: 16px">
                  成果报告
                </el-menu-item>
              </a>
              <a href="/Achievement_analysis">
                <el-menu-item index="2-2" style="font-size: 16px">
                  成果分析
                </el-menu-item>
              </a>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-scrollbar>
    </el-aside>

    <el-main class="class11">
      <el-row>
        <el-col :span="3">
          <div class="class12" @click="get_total_messages">
            全部教职工（{{ totalNumber }}人)
          </div>
        </el-col>
        <el-col :span="3">
          <el-form ref="form" :model="form" class="class13">
            <el-input
              placeholder="请输入成员姓名"
              v-model="form.searchName"
              style="width: 200px"
              class="input-with-select"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="onSubmit"
              ></el-button>
            </el-input>
          </el-form>
        </el-col>
        <el-col :span="3">
          <div class="class14">
            <a href="/Member_add">
              <el-button type="primary">+添加成员</el-button>
            </a>
          </div>
        </el-col>
      </el-row>

      <el-table
        :data="tableData"
        border="true"
        header-align="center"
        style="width: 1201px; top: 130px; left: 216px"
        :row-class-name="tableRowClassName"
        :header-cell-style="{ background: '#CEE9FF', color: '#0072DC' }"
      >
        <el-table-column
          label="姓名"
          width="200"
          header-align="center"
          align="center"
        >
          <template slot-scope="scope">
            <span v-if="!showEdit[scope.$index]"> {{ scope.row.name }}</span>
            <el-input v-if="showEdit[scope.$index]" v-model="scope.row.name">
            </el-input>
          </template>
        </el-table-column>
        <el-table-column
          label="部门"
          width="200"
          header-align="center"
          align="center"
        >
          <template slot-scope="scope">
            <span v-if="!showEdit[scope.$index]">
              {{ scope.row.department }}</span
            >
            <el-input
              v-if="showEdit[scope.$index]"
              v-model="scope.row.department"
            >
            </el-input>
          </template>
        </el-table-column>
        <el-table-column
          label="职称"
          width="200"
          header-align="center"
          align="center"
        >
          <template slot-scope="scope">
            <span v-if="!showEdit[scope.$index]"> {{ scope.row.title }}</span>
            <el-input
              v-if="showEdit[scope.$index]"
              v-model="scope.row.title"
            ></el-input>
          </template>
        </el-table-column>
        <el-table-column
          label="电子邮箱"
          width="300"
          header-align="center"
          align="center"
        >
          <template slot-scope="scope">
            <span v-if="!showEdit[scope.$index]"> {{ scope.row.email }}</span>
            <el-input
              v-if="showEdit[scope.$index]"
              v-model="scope.row.email"
            ></el-input>
          </template>
        </el-table-column>
        <el-table-column
          label="操作"
          header-align="center"
          width="300"
          align="center"
        >
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleUpdate(scope.$index, scope.row)"
              v-if="showBtn[scope.$index]"
            >
              保存
            </el-button>
            <el-button
              size="mini"
              @click="handleCancel(scope.$index, scope.row)"
              v-if="showBtn[scope.$index]"
            >
              取消
            </el-button>
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)"
              v-if="!showBtn[scope.$index]"
            >
              编辑
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
            >
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "Member_management",
  data() {
    return {
      input: "",
      tableData: [
        {
          name: "张英兰",
          department: "计算机学院",
          title: "院士",
          email: "zhangyinglan@buaa.edu.cn",
        },
        {
          name: "张丽丽",
          department: "计算机学院",
          title: "副教授",
          email: "zhanglili@buaa.edu.cn",
        },
        {
          name: "张笑笑",
          department: "计算机学院",
          title: "教授",
          email: "zhangxiaoxiao@buaa.edu.cn",
        },
      ],
      totalNumber: 3,
      showEdit: [],
      showBtn: [],
      form: {
        searchName: "",
      },
      search_messages: [
        {
          name: "张英兰",
          department: "计算机学院",
          title: "院士",
          email: "zhangyinglan@buaa.edu.cn",
        },
      ],
      total_messages: [
        {
          name: "张英兰",
          department: "计算机学院",
          title: "院士",
          email: "zhangyinglan@buaa.edu.cn",
        },
        {
          name: "张丽丽",
          department: "计算机学院",
          title: "副教授",
          email: "zhanglili@buaa.edu.cn",
        },
        {
          name: "张笑笑",
          department: "计算机学院",
          title: "教授",
          email: "zhangxiaoxiao@buaa.edu.cn",
        },
      ],
    };
  },
  methods: {
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 1) {
        return "success-row";
      }
      return "";
    },
    tableheaderRowClassName({ row, rowIndex }) {
      return "header-row";
    },
    handleDelete(index, row) {
      this.$confirm("此操作将永久删除该信息, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.tableData.splice(index, 1),
            this.$message({
              type: "success",
              message: "删除成功!",
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    handleEdit(index, row) {
      this.$set(this.showEdit, index, true);
      this.$set(this.showBtn, index, true);
    },
    handleCancel(index, row) {
      this.$set(this.showEdit, index, false);
      this.$set(this.showBtn, index, false);
    },
    handleUpdate(index, row) {
      this.$set(this.showEdit, index, false);
      this.$set(this.showBtn, index, false);
    },
    onSubmit() {
      this.tableData = this.search_messages;
    },
    get_total_messages() {
      this.tableData = this.total_messages;
    },
  },
  mounted() {
    document
      .querySelector("body")
      .setAttribute("style", "background-color:white");
  },
  beforeDestroy() {
    document.querySelector("body").removeAttribute("style");
  },
};
</script>

<style>
body {
  background-color: #e1e4e7;
}
</style>