<template>
  <el-container>
    <el-main>
      <el-row class="class144_c">
        <el-col :span="5">
          <a
            href=""
            v-on:click="change_Achievement_acknowlege"
            style="color: #999999"
            >我的成果
          </a>
        </el-col>

        <el-col :span="1"> &gt; </el-col>
        <el-col :span="3">
          <a href="" v-on:click="change_project" style="color: #999999">项目</a>
        </el-col>
        <el-col :span="1"> &gt; </el-col>
        <el-col :span="4">
          <a href="" v-on:click="change_Results_search" style="color: #0064f9"
            >项目搜索</a
          >
        </el-col>
      </el-row>

      <el-divider class="class107"></el-divider>
      <el-form ref="form" :model="form" class="class104">
        <el-input
          placeholder="请输入学者姓名"
          v-model="form.searchInput"
          class="input-with-select"
          style="background-color: white"
        >
          <el-select v-model="form.select" slot="prepend" style="width: 100px">
            <el-option
              v-for="select_category in select_categorys"
              :key="select_category.key"
              :label="select_category.key"
              :value="select_category.value"
            ></el-option>
          </el-select>
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </el-form>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "Results_search",
  data() {
    return {
      form: {
        select: "按学者",
        searchInput: "",
      },
      select_categorys: [
        {
          key: "按名字",
          value: 1,
        },
        {
          key: "按学者",
          value: 2,
        },
      ],
    };
  },
  methods: {
    change_Achievement_acknowlege() {
      this.$router.push({
        name: "Achievement_acknowlege",
        query: {
          id: this.id,
        },
      });
    },
    change_project() {
      this.$router.push({
        name: "project",
        query: {
          id: this.id,
        },
      });
    },
    change_Results_search() {
      this.$routerrouter.push({
        name: "change_Results_search",
        query: {
          id: this.id,
        },
      });
    },
  },
  mounted() {
    document
      .querySelector("body")
      .setAttribute("style", "background-color:white");
  },
  beforeDestroy() {
    document.querySelector("body").removeAttribute("style");
  },
};
</script>

