<template>
  <el-container>
    <el-main>
      <div>
        <span class="class137">
          <img style="size: 100%" src="imgs/教师端-登录/背景.png" />
        </span>
        <div class="class138">欢迎登录xx教师成果管理系统</div>
        <div class="class139">请使用您本人的账号密码登录</div>
        <el-form
          ref="ref"
          class="class141"
          :model="form"
          :rules="LoginformRules"
        >
          <el-form-item>
            <img src="imgs/教师端-登录/电话.png" class="class141_a" />
            <el-input
              v-model="form.tel"
              prop="tel"
              placeholder="请输入电话号码"
              type="telephone"
              class="class141_b"
            ></el-input>
          </el-form-item>
          <el-divider class="class148_a"></el-divider>
          <el-form-item>
            <img src="imgs/教师端-登录/密码.png" class="class141_c" />
            <el-input
              type="password"
              prop="password"
              v-model="form.password"
              placeholder="请输入登录密码"
              class="class141_d"
            >
            </el-input>
          </el-form-item>
          <el-divider class="class148_b"></el-divider>
          <el-form-item>
            <el-checkbox v-model="form.remember" class="class141_e"
              >记住密码</el-checkbox
            >
          </el-form-item>
          <el-form-item>
            <el-button type="primary" class="class142" v-on:click="do_Login()"
              >登录</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </el-main>
  </el-container>
</template>
<script>
export default {
  name: "Sign",
  data() {
    return {
      form: {
        tel: "",
        password: "",
        remember: "",
      },
      LoginformRules: {
        tel: [
          { required: true, message: "请输入登录名称", trigger: "blur" },
          {
            min: 10,
            max: 10,
            message: "电话号码必须为10位手机号",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入登录密码", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: "长度在 6 到 15 个字符",
            trigger: "blur",
          },
        ],
        persons: {},
      },
    };
  },
  methods: {
    do_Login() {
      var url = "/v1/scholars/getScholarByName/?scholarName=";
      url = url + "王德庆" + "&orgName=" + "北京航空航天大学";
      this.$axios
        .get(url)
        .then((response) => {
          this.persons = response.data.data.content[0];
          console.log(response);
          console.log(this.persons);
          // 传入成员信息对应的唯一标识
          this.$router.push({
            name: "Achievement_acknowlege",
            // query: {
            //   persons: this.persons,
            // },
          });
        })
        .catch((error) => {
          console.log("抱歉，失败啦");
          console.log(error);
        });
    },
  },
  mounted() {
    document
      .querySelector("body")
      .setAttribute("style", "background-color:white");
  },
  beforeDestroy() {
    document.querySelector("body").removeAttribute("style");
  },
};
</script>

