<template>
  <el-container>
    <el-main>
      <h2 class="class83">
        欢迎使用xx教师绩效考核系统，请先<span>导入成员</span>
      </h2>

      <!-- 此处为路由跳转类比
      <el-button type="primary" @click="UploadUrl"> 路由跳转 </el-button> -->

      <!-- 这里的UploadUrl需要和后端搭配使用
      必须得show-file-list=false在auto-upload=‘true’的前面，否则会出现无法页面跳转的情况。
      设置show-file-list=false的原因是为了隐藏上传后会显示文件 -->
      <el-upload
        action="https://jsonplaceholder.typicode.com/posts/"
        class="upload-demo"
        accept=".xlsx"
        :show-file-list="false"
        :auto-upload="true"
        :on-success="UploadUrl"
      >
        <el-button type="primary" class="class84">导入成员</el-button>
      </el-upload>
      <!-- 一定要放在public下才能下载 -->
      <a :href="'/files/成员信息模板.xlsx'" class="class85">下载导入模板</a>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "Member_import",
  data() {
    return {
      file: "",
    };
  },
  methods: {
    UploadUrl() {
      this.$router.push({ name: "Import_information_confirm" });
    },
  },
  mounted() {
    document
      .querySelector("body")
      .setAttribute(
        "style",
        "width: 100%; height: 100%; background-color:white"
      );
  },
  beforeDestroy() {
    document.querySelector("body").removeAttribute("style");
  },
};
</script>

<style>
.test1 {
  height: 0;
  padding-top: 15.16%;
  position: relative;
}
.test2 {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
}
</style>
