<template>
  <el-container>
    <el-aside width="220px" class="class0">
      <el-scrollbar style="height: 100%">
        <el-menu
          :default-openeds="['1', '2']"
          text-color="#fff"
          active-text-color="#ffd04b"
          style="top: 79px"
        >
          <a href="/Member_management">
            <el-menu-item>
              <img src="imgs/管理中心.png" class="class0_a" />
              <span slot="title"> 成员管理 </span>
            </el-menu-item>
          </a>

          <el-submenu index="1">
            <template slot="title">
              <img src="imgs/文化成果.png" class="class0_a" />
              <span style="color: white"> 成果管理 </span>
            </template>
            <el-menu-item-group>
              <a href="/Achievement">
                <el-menu-item index="2-1" style="font-size: 16px">
                  成果报告
                </el-menu-item>
              </a>
              <a href="/Achievement_analysis">
                <el-menu-item index="2-2" style="font-size: 16px">
                  成果分析
                </el-menu-item>
              </a>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-scrollbar>
    </el-aside>

    <el-container>
      <el-head class="class1">
        <a
          href="/Member_management"
          class="class4"
          style="text-decoration: none"
        >
          &lt; 返回
        </a>
      </el-head>

      <el-main class="class2">
        <el-row>
          <el-col :span="3">
            <div class="class3">添加成员</div>
          </el-col>
        </el-row>
        <el-divider class="class6"></el-divider>
        <el-form ref="form" :model="form" label-width="80px" class="class5">
          <el-form-item label="姓名：" style="text-align: left; width: 443px">
            <el-input
              v-model="form.name"
              placeholder="输入教职工姓名"
            ></el-input>
          </el-form-item>
          <el-form-item label="部门：" style="text-align: left; width: 443px">
            <el-input
              v-model="form.orgName"
              placeholder="输入教职工部门"
            ></el-input>
          </el-form-item>
          <el-form-item label="工号：" style="text-align: left; width: 443px">
            <el-input v-model="form.id" placeholder="输入教职工工号"></el-input>
          </el-form-item>
          <el-form-item label="邮箱：" style="text-align: left; width: 443px">
            <el-input
              v-model="form.email"
              placeholder="输入教职工邮箱"
            ></el-input>
          </el-form-item>
          <el-form-item label="职称：" style="text-align: left">
            <el-select
              v-model="form.title"
              placeholder="请选择职称"
              style="width: 363px"
            >
              <el-option label="院士" value="yuanshi"></el-option>
              <el-option label="教授" value="jiaoshou"></el-option>
              <el-option label="副教授" value="fujiaoshou"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="get_staff">立即查找</el-button>
          </el-form-item>
        </el-form>

        <el-row v-if="find == true">
          <el-col :span="3">
            <div class="class15">为您找到</div>
          </el-col>
          <el-divider class="class16"></el-divider>
        </el-row>

        <span
          class="class17"
          v-for="(card, index) in cards"
          :key="index"
          :style="{ top: 650 + Math.floor(index / 3) * 400 + 'px' }"
          :class="setClass(index)"
        >
          <el-row>
            <!-- 这里一定只能用:src，利用v-bind来显示 -->
            <img class="class18" :src="card.avg" />

            <el-col :span="6">
              <div class="class19">
                {{ card.name }}
              </div>
              <div class="class20">
                {{ card.orgName }}
              </div>
            </el-col>
            <el-col :span="6">
              <el-button type="primary" class="class21">
                <i class="el-icon-plus class21_a"></i>
              </el-button>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="2" class="class22">
              <img src="/imgs/添加教职工 2/最新论文.png" />
            </el-col>
            <el-col :span="5" class="class23">最新论文</el-col>
          </el-row>

          <el-row>
            <el-col :span="10">
              <div class="class25">
                {{ card.paperTitle }}
              </div>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="4">
              <div class="class26">
                {{ card.paperCount }}
              </div>
              <div class="class27">发表论文</div>
            </el-col>

            <el-col :span="4">
              <div class="class28">
                {{ card.patentCount }}
              </div>
              <div class="class29">申请专利</div>
            </el-col>

            <el-col :span="4">
              <div class="class30">
                {{ card.projectCount }}
              </div>
              <div class="class31">研究项目</div>
            </el-col>
          </el-row>
        </span>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "Member_add",
  data() {
    return {
      form: {
        name: "",
        orgName: "",
        id: "",
        email: "",
        title: "",
      },
      cards: [],
      img: "",
      find: false,
    };
  },
  methods: {
    onSubmit() {
      console.log("提交成功");
    },
    setClass(num) {
      return num % 3 == 0
        ? "class17_1"
        : num % 3 == 1
        ? "class17_2"
        : "class17_3";
    },
    get_staff() {
      var url = "/v1/scholars/getScholarByName/?scholarName=";
      url = url + this.form.name + "&orgName=" + this.form.orgName;
      this.$axios
        .get(url)
        .then((response) => {
          this.cards = response.data.data.content;
          console.log(response);
        })
        .catch((error) => {
          console.log("抱歉，失败啦");
          console.log(error);
        });
      this.find = true;
    },
  },
  mounted() {
    document
      .querySelector("body")
      .setAttribute("style", "background-color:white");
  },
  beforeDestroy() {
    document.querySelector("body").removeAttribute("style");
  },
};
</script>

<style>
</style>